<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { mediaState } from 'data/listBody'

const dataOption = {
  leftMenu: [
    { id:'1', name:'媒体标题：', placeholder:'请输入标题', module:'title'},
    { id:'2', name:'媒体类型：', placeholder:'请选择媒体类型', module:'select', type:'select', options: mediaState},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'媒体标题', prop:'title'},
    { label:'媒体类型', prop:'typeName'},
    { label:'媒体链接', prop:'url'},
  ],
  edit: [
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'medias',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`mediasAdd?type=new`);
    },
    tabchange(params){
      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`mediasAdd?type=edit&id=${this.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
    },
    sure(){
      this.api.delete(`/media/${this.id}`).then((res)=>{
        this.loading = false;
        this.search();
        this.$message(res.data);     
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };
      const params = Object.assign({}, optParams, pageParams);
      const param = queryString.stringify(params || {});

      this.loading = true;
      this.api.get(`/media?${param}`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.setPage(res.data);
        this.tableList = res.data.list;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//数据初始化
        const tableList = data.list.map(item => {
            if(item.type == 1) item.typeName = '音频';
            if(item.type == 2) item.typeName = '视频';
        })
        this.tableList = tableList;
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>
 